@use 'sass:math';
@use 'functions' as *;
@use './mixins/fluid' as *;
@use './mixins/disable-mob-hover' as *;

html {
  &.is-lock {
    overflow: hidden;
  }
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  font-style: normal;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
  background-color: var(--color-bg);
}

.page__body {
  margin: 0;
  min-width: 390px;
  min-height: 100%;
}

img {
  object-fit: cover;
}

.site-container {
  //overflow: hidden; // If you use position: sticky on your site, remove this setting.
}

.main {
  flex-grow: 1;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-header {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-header-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

a,
button,
label,
input,
textarea,
select,
svg * {
  transition-duration: var(--transition-duration);
}

a {
  color: inherit;

  &[class] {
    text-decoration: none;
  }
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  white-space: nowrap !important;
  clip-path: inset(100%) !important;
  clip: rect(0 0 0 0) !important;
  overflow: hidden !important;
}

.btn-primary {
  color: var(--color-white);
  font-size: 16px;
  line-height: normal;
  border-radius: 8px;
  background-color: var(--color-primary);
  transition: all 0.3s ease 0s;
  &:hover {
    background: rgba(182, 136, 103, 0.70);
  }
  &[disabled] {
    color: var(--color-white-50);
    background: rgba(182, 136, 103, 0.20);
  }
}

.btn-secondary {
  color: var(--color-white);
  font-size: 16px;
  line-height: normal;
  border-radius: 8px;
  background-color: var(--color-primary-20);
  transition: background-color 0.3s ease 0s;
  &:hover {
    background: var(--color-primary-20-hover);
  }
  &:focus-visible {
    outline: 1px solid var(--color-primary);
    outline-offset: 2px;
  }
  &:active {
    outline: 1px solid var(--color-primary);
  }
  &[disabled] {
    background-color: var(--color-primary-20-disabled);
    color: rgba(255, 255, 255, 0.50);
    pointer-events: none;
  }
}

.hide {
  display: none;
}
