@use './mixins/fluid' as *;
@use 'functions' as *;

@use "vars" as *;
@use "mixins" as *;
@use "fonts" as *;
@use "settings" as *;

// Header start ========================================================================================================================================================

.header {
  background-color: var(--color-05);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 101;
  transition: box-shadow 0.3s ease 0s;
  &.scrolling {
    box-shadow: 0 1px 2px rgba(#B68867, 0.5);
    .header__inner {
      padding-block: 8px;
    }
  }

  // .header__inner

  &__inner {
    padding-block: 16px;
    display: flex;
    column-gap: fluid(84, 16);
    align-items: center;
    transition: padding-block 0.3s ease 0s;
    @include laptop {
      flex-wrap: wrap;
      justify-content: flex-end;
      row-gap: 8px;
    }
  }

  // .header__logo

  &__logo {
    @include laptop {
      margin-right: auto;
    }
    @include tablet {
      margin: 0;
    }
  }

  // .header__nav

  &__nav {
    flex: 1;
    @include laptop {
      flex: 0;
    }
    > ul {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      column-gap: fluid(24, 16);
      > li {
        > a {
          color: var(--color-white);
          font-size: 16px;
          line-height: normal;
          text-decoration: none;
          transition: all 0.3s ease 0s;
          white-space: nowrap;
          @include hover {
            color: var(--color-primary);
          }
        }
      }
    }
    @include tablet {
      position: fixed;
      top: var(--header-height);
      right: 0;
      width: 390px;
      height: calc(100vh - var(--header-height));
      background-color: #2D2D2F;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 32px 24px;
      z-index: -1;
      transition: transform 0.3s ease 0s;
      transform: translateX(100%);
      overflow-y: auto;
      overflow-x: hidden;
      @include mobile-s {
        width: 100%;
      }
      &.menu--active {
        transform: translateX(0%);
      }
      > ul {
        flex-direction: column;
        > li {
          &:not(:last-child) {
            margin-bottom: 16px;
          }
          a {
            padding: 8px;
            color: var(--color-white-70);
            font-size: 24px;
            font-weight: 700;
          }
        }
      }
    }
  }

  // .header__mobile-actions

  &__mobile-actions {
    display: none;
    @include tablet {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 24px;
      margin-bottom: 30px;
    }
  }

  // .header__mobile-sign-up

  &__mobile-profile {
    @include tablet {
      grid-column: 1 / -1;
    }
  }

  // .header__mobile-phone

  &__mobile-phone {
    @include tablet {
      display: flex;
      align-items: center;
    }
  }

  // .header__mobile-lang

  &__mobile-lang {
    @include tablet {
      display: flex;
      justify-content: flex-end;
      .choices__inner {
        background-color: #2D2D2F !important;
      }
      .choices__list--dropdown {
        left: auto;
        right: 0%;
      }
    }
  }

  // .header__action

  &__action {
    display: flex;
    column-gap: fluid(48, 16);
    align-items: center;
    @include tablet {
      flex: 1;
      justify-content: flex-end;
    }
    .header__contact {
      @include mobile {
        display: none;
      }
    }
  }

  // .header__contact

  &__contact, &__profile {
    display: flex;
    align-items: center;
    column-gap: 4px;
    svg {
      path {
        transition: all 0.3s ease 0s;
      }
    }
    @include hover {
      svg {
        path {
          stroke: var(--color-primary);
        }
      }
      span {
        color: var(--color-primary);
      }
    }
  }

  // .header__phone

  &__phone, &__sign-in {
    color: var(--color-white-50);
    font-size: 16px;
    line-height: normal;
    transition: all 0.3s ease 0s;
}

  // .header__profile

  &__profile {
    @include tablet {
      border-radius: 8px;
      background: var(--color-primary);
      display: flex;
      justify-content: center;
      padding: 18px 24px;
      flex-direction: row-reverse;
      span {
        color: var(--color-white);
      }
      svg {
        path {
          stroke: var(--color-white);
        }
      }
    }
  }

  // Select languages

  .choices__inner {
    border: 0;
    background-color: var(--color-05);
    color: var(--color-white-50);
    font-size: 16px;
    line-height: normal;
    text-transform: uppercase;
  }

  .choices[data-type*=select-one]:after {
    border: 0;
    width: 14px;
    height: 8px;
    background: url('../img/chevron-down.svg') center / 14px 8px no-repeat;
    margin-top: 0;
    right: 0;
    transform-origin: 50% 0%;
    transform: translateY(-50%);
    transition: all 0.3s ease 0s;
  }
  .choices[data-type*=select-one].is-open:after {
    transform: rotate(180deg) translateY(-3px);
  }
  .choices__list--dropdown .choices__item {
    padding-left: 34px;
    background-position: 8px center;
    background-repeat: no-repeat;
    background-size: 18px 18px;
    &:nth-child(1) {
      background-image: url('../img/flag-united-kingdom.svg');
    }
    &:nth-child(2) {
        background-image: url('../img/flag-ukraine.svg');
    }
    &:nth-child(3) {
        background-image: url('../img/flag-croatia.svg');
    }
  }
  .choices__list--dropdown .choices__item--selectable.is-highlighted {
    background-color: #39393B;
    background-position: 8px center, calc(100% - 8px) center;
    background-repeat: no-repeat, no-repeat;
    background-size: 18px 18px, 20px 20px;
    &:nth-child(1) {
      background-image: url('../img/flag-united-kingdom.svg'), url('../img/check.svg');
    }
    &:nth-child(2) {
        background-image: url('../img/flag-ukraine.svg'), url('../img/check.svg');
    }
    &:nth-child(3) {
        background-image: url('../img/flag-croatia.svg'), url('../img/check.svg');
    }
  }

  .choices__list--dropdown {
    padding: 6px;
    border-radius: 8px;
    border: 1px solid #39393B;
    color: var(--color-white-50);
    background-color: var(--color-bg);
    font-size: 16px;
    line-height: normal;
    box-shadow: 0px 16px 20px 0px rgba(0, 0, 0, 0.50), 0px 4px 6px -2px rgba(16, 24, 40, 0.03);
    background-color: black;
    min-width: 180px;
    width: 100%;
  }
  .choices__list--dropdown .choices__item--selectable {
    text-transform: uppercase;
    padding-right: 10px;
    border-radius: 6px;
  }

  // Logged in

  .menu-logged {
    display: none;
  }
  &.logged {
    .header__profile {
      display: none;
    }
  }

  &.logged {
    .menu-logged {
      display: inline-flex;
      align-items: center;
      @include mobile-s {
        align-items: flex-start;
        .menu-logged__item {
          width: 100%;
        }
        .menu-logged__btn {
          padding-bottom: 24px;
          position: relative;
          width: 100%;
          position: relative;
          border-bottom: 1px solid #4E4E50;
          @include mobile-s {
            &:before {
              content: '';
              position: absolute;
              top: 100%;
              right: 100%;
              width: calc(100vw + var(--container-offset) / 2);
              height: 1px;
              background-color: #4E4E50;
            }
            &:after {
              top: 100%;
              left: 100%;
              width: calc(100vw + var(--container-offset) / 2);
              height: 1px;
              background-color: #4E4E50;
              transform: translateY(0%);
            }
          }
        }
        .dropdown {
          background-color: transparent;
          box-shadow: none;
          border: none;
          .dropdown__list {
            position: relative;
            padding-block: 15px;
            margin-bottom: 10px;
            border-bottom: 1px solid #4E4E50;
            @include mobile-s {
              &:before {
                content: '';
                position: absolute;
                top: 100%;
                right: 100%;
                width: calc(100vw + var(--container-offset) / 2);
                height: 1px;
                background-color: #4E4E50;
              }
              &:after {
                content: '';
                position: absolute;
                top: 100%;
                left: 100%;
                width: calc(100vw + var(--container-offset) / 2);
                height: 1px;
                background-color: #4E4E50;
              }
            }
          }
          .dropdown__item {
            &:not(:last-child) {
              margin-bottom: 16px;
            }
            .dropdown__link {
              font-size: 24px;
              font-weight: 700;
              padding: 0;
              svg {
                display: none;
              }
            }
            &:last-child {
              display: none;
            }
          }
        }
      }

      // .menu__item

      &__item {
        position: relative;
        z-index: 1;
        &:not(:last-child) {
          margin-right: 30px;
        }
      }

      // .menu__btn

      &__btn {
        cursor: pointer;
        color: var(--color-white-50);
        font-size: 16px;
        line-height: normal;
        position: relative;
        padding-right: 22px;
        display: flex;
        column-gap: 8px;
        align-items: center;
        span {
          @include square(40px);

          display: flex;
          align-items: center;
          justify-content: center;
          background-color: #4F4F51;
          padding: 8px;
          border-radius: 50%;
          color: var(--color-white-70);
          font-size: fluid(16, 20);
          font-style: normal;
          font-weight: 600;
          line-height: var(--Line-height-text-md, 24px); /* 150% */
        }
        &:after {
          content: '';
          position: absolute;
          right: 0;
          top: 50%;
          width: 14px;
          height: 8px;
          background: url('../img/chevron-down.svg') center / 14px 8px no-repeat;
          margin-top: 0;
          transform-origin: 50% 0%;
          transform: translateY(-50%);
          transition: all 0.3s ease 0s;
        }
        &-active {
          &:after {
            transform: rotate(180deg) translateY(-3px);
          }
        }
      }
    }

    .dropdown {
      position: absolute;
      left: 0;
      top: 100%;
      z-index: 1020;
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.3s ease 0s, visibility 0.3s ease 0s;
      padding: 6px;
      border-radius: 8px;
      border: 1px solid #39393B;
      color: var(--color-white-50);
      background-color: var(--color-bg);
      font-size: 16px;
      line-height: normal;
      box-shadow: 0px 16px 20px 0px rgba(0, 0, 0, 0.50), 0px 4px 6px -2px rgba(16, 24, 40, 0.03);
      max-width: 195px;
      width: 100%;
      @include mobile-s {
        position: static;
        opacity: 1;
        visibility: visible;
        max-width: none;
        width: 100%;
      }
      &-active {
        opacity: 1;
        visibility: visible;
      }
      &__item {
        border-radius: 6px;
        overflow: hidden;
      }

      // .dropdown__link

      &__link {
        width: 100%;
        height: 100%;
        display: inline-flex;
        column-gap: 8px;
        width: 100%;
        padding: 11px 8px;
        color: var(--color-white-70);
        font-size: 14px;
        line-height: 1.4;
        transition: background-color 0.3s ease 0s;
        @include hover {
          background-color: #39393B;
        }
      }
    }
  }
}

.burger {

  @include burger;

  display: none;

  @include tablet {
    display: block;
  }
}

// Header end ========================================================================================================================================================

// Section Contact us start ========================================================================================================================================================


.contact {
  background: radial-gradient(rgba(0,0,0,0.4), transparent ), var(--bg-image-desktop) center/cover no-repeat;
  padding: 112px 0 120px;

  @include mobile {
    background: var(--bg-image-tablet) center/cover no-repeat;
  }

  @include mobile-s {
    background: var(--bg-image-mobile) center/cover no-repeat;
  }

  // .contact__list

  &__list {
    display: inline-flex;
    flex-direction: row-reverse;
    margin-bottom: 32px;
  }

  // .contact__item

  &__item {
    --personItemSize: 56px;
    position: relative;
    &:not(:last-child) {
      margin-left: calc(var(--personItemSize) / 4 * -1);
    }
    @for $i from 1 through 20 {
      &:nth-child(#{$i}) {
        z-index: $i;
      }
    }
  }

  // .contact__avatar

  &__avatar {
    width: var(--personItemSize);
    height: var(--personItemSize);
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover
    }
  }

  // .contact__online

  &__online {
    @include square(10px);

    border-radius: 50%;
    border: 1px solid var(--color-white);
    background-color: var(--color-red);
    position: absolute;
    right: 2px;
    bottom: 5px;
    &.active {
      background-color: var(--color-green);
    }
  }

  // .contact__title

  &__title {
    color: var(--color-white);
    font-size: 20px;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 8px;
  }

  // .contact__subtitle

  &__subtitle {
    margin-bottom: 32px;
    max-width: 450px;
    p {
      color: var(--color-white);
      font-size: 16px;
      line-height: normal;
    }
  }

  // .contact__btn

  &__btn {
    padding: 12.5px 26.5px;
  }
}

// Section Contact us end ========================================================================================================================================================

// Section product start ========================================================================================================================================================

.product {
  margin-top: var(--header-height);
  padding-block: fluid(64, 40) fluid(120, 80);
  overflow: hidden;
  @include mobile {
    padding-top: 0;
  }

  // .product__breadcrumb

  &__breadcrumb {
    margin-bottom: 40px;
    @include laptop {
      text-align: center;
    }
    @include mobile {
      display: none;
    }
    > li {
      position: relative;
      display: inline;
      line-height: 1.4;
      font-size: 16px;
      color: var(--color-white-50);
      &:not(:last-child) {
        padding-right: 12px;
        margin-right: 12px;
        &:after {
          content: '';
          position: absolute;
          left: 100%;
          top: 50%;
          transform: translateY(-50%);
          width: 6px;
          height: 10px;
          background: url('../img/icon-breadcrumbs.svg') 100% center/6px 10px no-repeat;
          color: var(--color-white-20);
        }
      }
      &:last-child {
        color: var(--color-white);
        &:after {
          content: '';
        }
      }
      > a {
        text-decoration: none;
        transition: color 0.3s ease 0s;
        &:hover {
          color: var(--color-white);
        }
      }
    }
  }

  .progress-container {
    max-width: 700px;
    margin: 0 auto;
    position: relative;
    margin-top: 16px;
    @include mobile {
      margin-bottom: 32px;
      &:after {
        content: '';
        position: absolute;
        left: -33px;
        top: -16px;
        width: calc(100% + 66px);
        height: calc(100% + 32px);
        background-color: var(--color-05);
        z-index: -1;
        border-bottom: 1px solid var(--color-white-20);
        border-top: 1px solid var(--color-white-20);
      }
    }
    &.hide {
      display: none;
    }
    &:before {
      content: '';
      height: 1px;
      width: var(--progress-width);
      background-color: var(--color-white-50);
      position: absolute;
      top: calc(var(--step-size) / 2);
      left: 50%;
      translate: -50% -50%;
    }
    > ol {
      list-style-type: none;
      padding: 0;
      margin: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      counter-reset: step-number;
      position: relative;
      z-index: 3;
      > li {
        display: flex;
        flex: 1;
        align-items: stretch;
        flex-direction: column;
        align-items: center;
        gap: 8px;
        color: var(--color-white-50);
        font-size: 12px;
        line-height: normal;
        &:where(.done, .current) {
          color: #fff;
        }
        &.current {
          &:before {
            background-color: var(--color-primary);
            border: 1px solid var(--color-white);
            outline: 6px solid rgba(182, 136, 103, 0.20);
          }
        }
        &.done {
          &:before {
            background: url('../img/check.png') center/16px 16px no-repeat var(--color-primary);
            width: 24px;
            height: 24px;
            transform: scale(1);
          }
        }
        &:before {
          counter-increment: step-number;
          content: counter(step-number);
          width: var(--step-size);
          height: var(--step-size);
          background-color: var(--color-white-50);
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 50%;
          color: #fff;
          font-size: 0;
          margin-bottom: 12px;
          transform: scale(0.5);
          transition: transform 0.5s ease 0s;
        }
      }
    }
    .progress {
      height: 1px;
      width: var(--progress-width);
      background-color: var(--color-primary);
      position: absolute;
      top: calc(var(--step-size) / 2);
      left: 50%;
      translate: -50% -50%;
      z-index: 2;
      transform: scaleX(0);
      transform-origin: left;
      transition: all 0.5s ease 0s;
    }
  }

  .controls {
    display: none;
    justify-content: center;
    align-items: center;
    button {
      padding: 18px 24px;
      @include mobile-s {
        min-width: 100% !important;
      }
    }
    .buttons-pay {
      button {
        &:not(:last-child) {
          margin-right: 16px;
          @include mobile-s {
            margin-bottom: 16px;
          }
        }
      }
      @include mobile {
        margin-top: 32px;
      }
      @include mobile-s {
        width: 100%;
      }
    }
    @include mobile {
      display: flex;
      margin-top: 15px;
      .btn-continue {
        &.hide {
          display: none;
        }
      }
    }
  }

  .steps-container {
    display: grid;
    grid-template-columns: auto 448px;
    column-gap: 48px;
    @include laptop {
      max-width: 968px;
      margin: 0 auto;
    }
    .step {
      height: fit-content;
    }
    .step-top {
      height: fit-content;
      grid-column: 1 / 2;
      @include mobile {
        align-self: center;
      }
    }
    > .step:nth-child(2) {
      grid-column: 1 / 2;
    }
    > .step:nth-child(3) {
      grid-column: 1 / 2;
    }
    > .step:nth-child(4) {
      grid-column: 2 / 3;
      grid-row: 1 / 4;
      @include laptop {
        grid-column: 1 / 2;
        grid-row: auto;
        margin-top: 32px;
      }
    }
    @include laptop {
      grid-template-columns: 1fr;
      column-gap: 0;
    }
    @include mobile {
      display: flex;
      overflow: hidden;
      .step-top {
        flex: 1 0 100%;
      }
      .step {
        flex: 1 0 100%;
      }
    }

    // Product car start

    .product-car {
        padding: 48px 24px 0px 24px;
        border-radius: 16px 16px 0 0;
        background: url('../img/ellipse-dark.png') right bottom/603px 247px no-repeat, url('../img/ellipse-light.png') right bottom/621px 482px no-repeat;
        background-color: var(--color-05);
        @include mobile {
          background-color: transparent;
          padding-left: 0;
          padding-right: 0;
          background: url('../img/ellipse-dark.png') center center/100% no-repeat, url('../img/ellipse-light.png') center -50px/80% no-repeat;
        }
        @include mobile-m {
          background: url('../img/ellipse-dark.png') center center/100% no-repeat, url('../img/ellipse-light.png') center -50px/100% no-repeat;
        }

        // .product-car__wrap

        &__wrap {
          border-bottom: 1px solid var(--color-white-20);
          padding-bottom: 40px;
          display: grid;
          grid-template-columns: auto 460px;
          align-items: center;
          @include tablet {
            grid-template-columns: auto auto;
          }
          @include mobile {
            border-bottom: 0;
          }
          @include mobile-m {
            display: flex;
            flex-direction: column-reverse;
            row-gap: 24px;
            width: 100%;
          }
        }

        // .product-car__body

        &__body {
          color: var(--color-white);
          @include mobile-m {
            align-self: flex-start;
          }
        }

        // .product-car__title

        &__title {
          @include fluid-text(32, 20);

          font-weight: 600;
          line-height: 100%;
          text-transform: capitalize;
          margin-bottom: 16px;
        }

        // .product-car__price

        &__price {
          font-size: 16px;
          line-height: normal;
          margin-bottom: 20px;
        }

        // .product-car__price-price

        &__price-price {
          @include fluid-text(24, 20);

          margin-right: 8px;
        }

        // .product-car__currency

        &__currency {
          text-transform: uppercase;
        }

        // .product-car__included

        &__included {
          @include fluid-text(20, 18);
          line-height: 1.5;
          margin-bottom: 16px;
        }

        // .product-car__list

        &__list {
          margin-bottom: 32px;
        }

        // .product-car__item

        &__item {
          font-size: 14px;
          font-weight: 300;
          line-height: normal;
          padding-left: 24px;
          background: url('../img/check-circle.svg') left center/16px 16px no-repeat;
          &:not(:last-child) {
            margin-bottom: 8px;
          }
        }

        // .product-car__info-list

        &__info-list {
          display: grid;
          grid-template-columns: 142px 142px;
          gap: 4px 8px;
        }

        // .product-car__info-item

        &__info-item {
          padding: 4px 4px 4px 0;
          display: flex;
          align-items: center;
          column-gap: 8px;
        }
    }

    // Product card end

    // Extras block start

    .extras {
        padding: 16px 24px 0 24px;
        background-color: var(--color-05);
        @include mobile {
          background-color: transparent;
        }
        @include mobile-s {
          padding-left: 0;
          padding-right: 0;
        }

        // .extras__wrap

        &__wrap {
          padding-bottom: 40px;
          border-bottom: 1px solid var(--color-white-20);
          @include mobile {
            border: 0;
          }
        }

        // .extras__title

        &__title {
          @include fluid-text(24, 20);

          line-height: normal;
          margin-bottom: 32px;
        }

        // .extras__list

        &__list {
          display: grid;
          grid-template-columns: repeat(auto-fit, minmax(294px, 1fr));
          gap: 24px 16px;
          @include mobile {
            grid-template-columns: repeat(auto-fit, minmax(176px, 1fr));
          }
          @include mobile-s {
            gap: 15px 6px;
          }
        }

        // .extras__item

        &__item {
          background-color: #39393B;
          border-radius: 8px;
          overflow: hidden;
        }

        // .extras__input

        &__input {
          display: none;
          &:checked ~ .extras__label {
            background-color: var(--color-primary);
            &:after {
              content: '\FF0D';
              background-color: rgba(197, 160, 133, 1);
            }
          }
        }

        // .extras__label

        &__label {
          display: flex;
          align-items: center;
          padding: 8px;
          column-gap: 8px;
          position: relative;
          @include mobile {
            flex-direction: column;
            align-items: flex-start;
            row-gap: 8px;
            height: 100%;
            padding: 8px 34px 8px 16px;
          }
          &:after {
            content: '\FF0B';
            position: absolute;
            right: 0;
            top: 0;
            background-color: var(--color-white-20);
            width: 34px;
            height: 100%;
            font-size: 32px;
            line-height: 1.4;
            display: flex;
            align-items: center;
            justify-content: center;
          }
          svg {
            align-self: stretch;
            flex-basis: 24px;
          }
        }

        // .extras__label-body

        &__label-body {
          display: flex;
          flex-direction: column;
          row-gap: 17px;
          padding-left: 16px;
          border-left: 1px solid rgba(255, 255, 255, 0.20);
          @include mobile {
            border-left: 0;
            padding-left: 0;
            align-items: flex-start;
            justify-content: space-between;
            flex-grow: 1;
          }
        }

        // .extras__label-name

        &__label-name {
          @include fluid-text(16, 14);
          color: var(--color-white);
          line-height: normal;
        }

        // .extras__label-price

        &__label-price {
          color: var(--color-white);
          font-size: 20px;
          font-weight: 700;
          line-height: normal;
        }
    }

    // Extras block end

    // Insurance block start

    .insurance {
      padding: 16px 24px 40px 24px;
      background-color: var(--color-05);
      border-radius: 0 0 16px 16px;
      @include mobile {
        background-color: transparent;
        padding-left: 0;
        padding-right: 0;
      }

        // .insurance__title

        &__title {
          @include fluid-text(24, 20);

          line-height: normal;
          margin-bottom: 32px;
        }

        // .insurance__list

        &__list {
          display: grid;
          grid-template-columns: repeat(3, 1fr);
          gap: 16px;
          @include mobile {
            grid-template-columns: 1fr;
          }
        }

        // .insurance__item

        &__item {
          position: relative;
        }

        // .insurance__input

        &__input {
          display: none;
          &:checked ~ .insurance__label + .card-insurance {
            background: url('../img/card-bg.png') right bottom/296px 327px no-repeat;
            background-color: var(--color-primary);
            .card-insurance__list:not(.card-insurance__list-alert) .card-insurance__item {
              background: url('../img/check-circle.svg') left 4px/16px 16px no-repeat;
            }
            .card-insurance__list.card-insurance__list-alert .card-insurance__item {
              background: url('../img/alert-circle-white.svg') left 4px/16px 16px no-repeat;
            }
            .card-insurance__recommend {
              color: var(--color-white);
            }
          }
        }

        // .insurance__label

        &__label {
          position: absolute;
          inset: 0;
        }

        .card-insurance {
          border-radius: 16px;
          background: rgba(255, 255, 255, 0.05);
          color: var(--color-white);
          padding: 24px;
          height: 100%;
          @include tablet {
            padding: 24px 16px;
          }
          // .card-insurance__title

          &__title {
            font-size: 16px;
            text-align: center;
            line-height: normal;
            margin-bottom: 8px;
          }

          // .card-insurance__price

          &__price {
            text-align: center;
            font-size: 32px;
            white-space: nowrap;
            font-weight: 600;
            line-height: 1;
            text-transform: capitalize;
            margin-bottom: 24px;
          }

          // .card-insurance__important

          &__important {
            font-size: 14px;
            line-height: 1.4;
            margin-bottom: 8px;
          }

          // .card-insurance__list-alert

          &__list-alert {
            .card-insurance__item {
              background: url('../img/alert-circle.svg') left 4px/16px 16px no-repeat;
            }
          }

          // .card-insurance__item

          &__item {
            padding-left: 24px;
            background: url('../img/check-circle-broken.svg') left 4px/16px 16px no-repeat;
            font-size: 14px;
            line-height: 1.4;
            &:not(:last-child) {
              margin-bottom: 8px;
            }
          }

          // .card-insurance__recommend

          &__recommend {
            color: #656566;
            font-size: 12px;
            font-weight: 300;
            line-height: 1.4;
            margin-top: 16px;
          }

          // .card-insurance__currency

          &__currency {
            text-transform: uppercase;
            margin-left: 8px;
          }
      }
    }

    // Insurance block end

    // Form order start

    .order {
        color: var(--color-white);

        // .order__title

        &__title {
          @include fluid-text(24, 20);

          line-height: normal;
          margin-bottom: 32px;
        }

        // .order__form

        &__form {
          color: var(--color-white);
          padding-inline: 1px;
          .buttons-pay-order {
            display: flex;
            column-gap: 24px;
            button {
              width: 100%;
              padding: 18px 24px;
            }
            @include mobile {
              display: none;
            }
          }
        }

        // .order__field

        &__field {
          &:not(.order__field-last) {
            margin-bottom: 24px;
          }
          input {
            width: 100%;
            outline: none;
            border: none;
            background-color: transparent;
            color: var(--color-white);
            font-size: 16px;
            line-height: 1.5;
            &::placeholder {
              color: var(--color-white-50);
            }
          }
          label {
            display: block;
            font-size: 14px;
            line-height: 1.4;
            margin-bottom: 6px;
            span {
              color: var(--color-primary);
              margin-left: 2px;
            }
          }
          &-last {
            .order__field-wrap {
              display: flex;
              column-gap: 20px;
              margin-bottom: 32px;
              select {
                color: var(--color-white-20);
                font-size: 16px;
                line-height: 1.5;
                border: none;
                outline: none;
                background-color: var(--color-bg);
              }
            }
          }
        }

        // .order__field-group

        &__field-wrap {
          padding: 12px 14px;
          border-radius: 8px;
          border: 1px solid var(--color-white-20);
          box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05);
        }

        // .order__accept

        &__accept {
          margin-bottom: 32px;
          .checkbox {
            position: relative;
            display: flex;
            align-items: center;
            column-gap: 12px;
            &::selection {
              background: none;
            }
          }
          .checkbox-field {
            appearance: none;
            display: none;
          }
          .checkbox-custom {
            display: inline-block;
            cursor: pointer;
            width: 20px;
            height: 20px;
            overflow: hidden;
            border: 1px solid var(--color-primary);
            border-radius: 6px;
            position: relative;
            &:after {
              content: '';
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              width: 20px;
              height: 20px;
              background: url('../img/check-white.svg') center center/14px 14px no-repeat var(--color-primary);
              opacity: 0;
              transition: opacity 0.3s ease 0s;
            }
          }

          .checkbox-field:checked + .checkbox-custom:after {
            opacity: 1;
          }

          .checkbox-field:disabled ~ .checkbox-content,
          .checkbox-field:disabled ~ .checkbox-custom {
            opacity: 0.4;
            pointer-events: none;
          }

          .checkbox-content {
            &::selection {
              background: none;
            }
          }
        }
    }

    .summary {
      border-radius: 16px;
      background: rgba(255, 255, 255, 0.04);
      padding: 16px;
      margin-bottom: 32px;
      @include mobile {
        margin-bottom: 0;
      }
      p {
        margin-bottom: 0;
      }

      // .summary__item

      &__item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        column-gap: 20px;
        color: var(--color-white);
        font-size: 14px;
        line-height: 1.4;
        &:not(:last-child) {
          margin-bottom: 7px;
        }
        &:last-child {
          font-size: 18px;
          font-style: normal;
          font-weight: 500;
          line-height: 1.55;
          border-top: 1px solid var(--color-white-20);
          margin-top: 24px;
          padding-top: 13px;
        }
      }

      // .summary__location

      &__location {
        > div {
          span {
            display: inline-block;
            line-height: 1;
            white-space: nowrap;
            width: 120px;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }
      }
    }

    // Form order end

  }

     // Selected car start

     .car-selected {
      display: flex;
      align-items: center;
      column-gap: 23px;
      margin-top: 16px;
      padding: 8px;

      // .car-selected__body

      &__body {
        color: var(--color-white);
      }

      // .car-selected__model

      &__model {
        font-size: 18px;
        font-weight: 500;
        line-height: 1.55;
        margin-bottom: 4px;
      }

      // .car-selected__similar

      &__similar {
        color: var(--color-white-50);
        font-size: 14px;
        line-height: 1.4;
      }

      // .car-selected__day

      &__day {
        font-size: 14px;
        line-height: 1.4;
      }
  }

  // Selected car end

}

// Section product end ========================================================================================================================================================

// Footer start ========================================================================================================================================================

.footer {

  // .footer__body

  &__body {
    padding-block: fluid(64, 40);
    display: flex;
    justify-content: space-between;
    column-gap: 30px;
    border-bottom: 1px solid var(--color-white-20);
    @include mobile-m {
      flex-direction: column;
      row-gap: 32px;
    }
  }

  // .footer__logo

  &__logo {
    margin-bottom: 24px;
  }

  // .footer__subtitle

  &__subtitle {
    p {
      color: var(--color-white-50);
      font-size: 16px;
      line-height: 1.5;
    }
  }

  // .footer__subscribe

  &__subscribe {
    max-width: 460px;
  }

  // .footer__form

  &__form {
    display: grid;
    grid-template-columns: minmax(237px, auto) minmax(124px, auto);
    column-gap: 16px;
    label {
      display: block;
      margin-bottom: 6px;
      color: var(--color-white);
      font-size: 14px;
      line-height: 1.4;
      grid-column:  -1 / 1;
      row-gap: 16px;
    }
    input {
      color: var(--color-white);
      font-size: 16px;
      line-height: 1.5;
      border-radius: 8px;
      border: 1px solid var(--color-white-20);
      box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05);
      background-color: transparent;
      padding: fluid(11, 11) fluid(14, 12);
      &::placeholder {
        color: var(--color-white-50);
      }
    }
    @include mobile-s {
      grid-template-columns: minmax(237px, auto) minmax(108px, auto);
    }
  }

  // .footer__btn

  &__btn {
    padding: fluid(14, 14) fluid(24, 16);
  }

  // .footer__copyright

  &__copyright {
    padding-block: fluid(32, 32) fluid(48, 40);
  }

  // .footer__copy

  &__copy {
    display: flex;
    justify-content: space-between;
    align-items: center;
    & * {
      color: var(--color-white);
      font-size: fluid-text(16, 14);
      line-height: 1.5;
      @include mobile-s {
        color: var(--color-white-50);
        line-height: 1.4;
      }
    }
    & a {
      &:hover {
        text-decoration: underline;
      }
    }
    > p {
      margin-bottom: 0;
    }
    @include mobile-m {
      flex-direction: column;
      align-items: flex-start;
      row-gap: 24px;
    }
  }

  // .footer__list

  &__list {
    display: flex;
    column-gap: 15px;
  }
}

// Footer end ========================================================================================================================================================






